import React, { useState, useEffect, useCallback } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import MaterialTable from 'material-table';
import Page from '../../../components/Page';
import useIsMountedRef from '../../../hooks/useIsMountedRef';
import useRank from '../../../hooks/useRank';
import { Container, Box, IconButton, SvgIcon, makeStyles } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { Edit as EditIcon, Trash } from 'react-feather';
import rankService from '../../../services/rankService';
import DeleteConfirmCheck from '../../../components/DeleteConfirmCheck';
import { useTranslation } from 'react-i18next';
import Header from './Header';
// import Results from './Results';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

export default function RankList() {
  const { t } = useTranslation();
  const { ranks, deleteRank } = useRank();
  // const [ranks, setRanks] = React.useState([]);

  const classes = useStyles();

  const [open, setOpen] = React.useState(false);
  const [needDeleteRank, setDeleteRank] = React.useState({});

  const handleClickOpen = (rank) => {
    setOpen(true);
    setDeleteRank(rank);
  };

  const handleDeleteRank = async () => {
    await deleteRank(needDeleteRank.id);
    setOpen(false);
    setDeleteRank({});
  };

  const handleClose = () => {
    setOpen(false);
  };

  const columns = [
    {
      title: t('Name'),
      field: 'name'
    },
    { title: t('Rank'), field: 'rank' },
    {
      title: t('Actions'),
      field: 'actions',
      render: (rowData) => (
        <>
          <IconButton component={RouterLink} to={`/ranks/${rowData.id}/edit`}>
            <SvgIcon fontSize="small">
              <EditIcon />
            </SvgIcon>
          </IconButton>
          <IconButton onClick={() => handleClickOpen(rowData)}>
            <SvgIcon fontSize="small">
              <Trash />
            </SvgIcon>
          </IconButton>
        </>
      )
    }
  ];

  if (!ranks) {
    return null;
  }

  return (
    <Page className={classes.root} title={t('Rank List')}>
      <Container maxWidth={false}>
        <Header />
        {ranks && (
          <Box mt={3}>
            <MaterialTable title={t('Rank List')} columns={columns} data={ranks} />
          </Box>
        )}
      </Container>
      <DeleteConfirmCheck
        open={open}
        handleClose={handleClose}
        handleDelete={handleDeleteRank}
        checkText={needDeleteRank.name}
      />
    </Page>
  );
}
